<script>
    /**
     *  陈斌
     *  获取核查结果并展示
     */
    import axios from "axios";

    export default {
        name: "check-result",

        data() {

            return {
                resultData: '',
                dataJson: {},
                transactionData: [],
                totalRows: 1,
                currentPage: 1,
                perPage: 25,
                pageOptions: [25, 50],
                filter: null,
                filterOn: [],
                sortBy: "orderid",
                sortDesc: false,
                fields: [
                    { key: "orderid", sortable: true, label: "检测条目" },
                    { key: "date", sortable: true },
                    { key: "billingname", sortable: true, label: "加固建议" },
                    { key: "total", sortable: true, label: "推荐值" },
                    { key: "paymentstatus", sortable: true, label: "检查结果" },
                    { key: "action" , label: "查看"}
                ]
            };
        },

        methods: {

            getResult() {
                //get为请求方式，请求路径为baseURL+''即http://localhost:8088/student/index
                //then回调函数，res为请求返回的结果
                axios.get('http://localhost:8848/host2task/getCheckResult',
                    {params:{'taskId': 1}})
                    .then(res => {
                        // console.log(res);
                        // 获取到数据, 再将其处理为我们需要的格式
                        this.resultData = res.data.checkResult.baselineCheck;
                        this.getDataFormal();
                    }).catch(function(error) {
                    console.log(error);
                })
            },

            onFiltered(filteredItems) {
                // Trigger pagination to update the number of buttons/pages due to filtering
                this.totalRows = filteredItems.length;
                this.currentPage = 1;
            },

            // 将请求的数据转化为标准需要的格式
            getDataFormal(){

                for(let s in this.resultData['核查结果']){

                    this.dataJson = {
                        orderid: this.resultData['核查结果'][s]['item_name'],
                        date: "28 Mar, 2020",
                        billingname: this.resultData['核查结果'][s]['加固建议'],
                        total: this.resultData['核查结果'][s]['proposed_value(推荐值)'],
                        paymentstatus: this.resultData['核查结果'][s]['检查结果'],
                    }
                    this.transactionData = this.transactionData.concat(this.dataJson);
                    // console.log('---', this.resultData['baselineCheck'][s]['item_name']);

                }
                // console.log('---', this.transactionData);
            }
        //    methods 结束
        },

        computed: {
            /**
             * Total no. of records
             */
            rows() {
                return this.transactionData.length;
            }
        },

        mounted() {
            // Set the initial number of items
            this.totalRows = this.transactionData.length;
        },

        // 调用方法
        created: function () {
          this.getResult();

        },


    }

</script>

<template>
    <div class="card">
        <div class="card-body" >
            <b-dropdown right toggle-class="arrow-none card-drop" class="float-right" variant="white">
                <template v-slot:button-content>
                    <i class="mdi mdi-dots-vertical"></i>
                </template>
            </b-dropdown>

            <h4 class="card-title mb-4" style="font-size: 20px">检测结果样例</h4>
            <div class="row mt-4">
                <div class="col-sm-12 col-md-6">
                    <div id="tickets-table_length" class="dataTables_length">
                        <label class="d-inline-flex align-items-center">
                            Show&nbsp;
                            <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
                        </label>
                    </div>
                </div>
                <!-- Search -->
                <div class="col-sm-12 col-md-6">
                    <div id="tickets-table_filter" class="dataTables_filter text-md-right">
                        <label class="d-inline-flex align-items-center">
                            <!--              查找接口-->
                            Search:
                            <b-form-input
                                    v-model="filter"
                                    type="search"
                                    class="form-control form-control-sm ml-2"
                            ></b-form-input>
                        </label>
                    </div>
                </div>
                <!-- End search -->
            </div>
            <div class="table-responsive">
                <b-table
                        :items="transactionData"
                        :fields="fields"
                        responsive="sm"
                        :per-page="perPage"
                        :current-page="currentPage"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :filter="filter"
                        :filter-included-fields="filterOn"
                        @filtered="onFiltered"
                >
                    <template v-slot:cell(paymentstatus)="row">
                        <div
                                class="badge font-size-12"
                                :class="{'badge-soft-danger': `${row.value}` === '不合格',
              // 'badge-soft-warning': `${row.value}` === '合格',
              'badge-soft-success': `${row.value}` === '合格'
              }"
                        >{{ row.value}}</div>
                    </template>

                    <template v-slot:cell(action)>
                        <!--            修改为查看接口-->
                        <a
                                href="javascript:void(0);"
                                class="mr-3 text-primary"
                                v-b-tooltip.hover
                                data-toggle="tooltip"
                                title="Edit"
                        >
                            <i class="mdi mdi-pencil font-size-18"></i>
                        </a>

                        <!--            <a href="javascript:void(0);" class="text-danger" v-b-tooltip.hover title="Delete">-->

                        <!--              <i class="mdi mdi-trash-can font-size-18"></i>-->
                        <!--            </a>-->

                    </template>
                </b-table>
            </div>
            <div class="row">
                <div class="col">
                    <div class="dataTables_paginate paging_simple_numbers float-right">
                        <ul class="pagination pagination-rounded mb-0">
                            <!-- pagination -->
                            <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

